<template>
    <div class="integralContainer">
        <div class="integral">
            <div>
                <p class="line1">当前积分 <span class="glyphicon glyphicon-question-sign" @click="goToIntegralInfo()">积分说明</span></p>
                <p class="line2"><b>{{integral}}</b>分</p>
                <div class="withdraw" @click="goToIntegralShop()">积分兑换商品</div>
            </div>
        </div>
        <div class="dealList">
            <h4>最近30天积分记录</h4>
            <div>
                <img src="data:image/png;base64," alt="">
                <p>最近30天无积分记录</p>
                <p>快去下单赚取大量积分吧</p>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data(){
        return {
            integral: 0.00
        }
    },
    methods:{
        goToIntegralInfo(){
            this.$router.push({name: 'answer', query:{key: 'pointtextCaption'}})
        },
        goToIntegralShop(){
            if(this.integral > 0){
                this.$router.push({name: 'integralShop'})
            }else{
                // TODO 弹窗提示赚取积分
            }
        }
    }
}
</script>

<style lang='scss' scoped>
    .integralContainer{
        background-color: #f5f5f5;
        .integral{
            background-color: #3190e8;
            padding: 10px;
            div{
                padding: 10px;
                background-color: #fff;
                border-radius: 10px;
                .line1{
                    margin: 0;
                    color: #666666;
                    .glyphicon{
                        float: right;
                        color: #3190e8;
                        font-size: 12px;
                    }
                }
                .line2{
                    margin: 0;
                    b{
                        font-size: 35px;
                        color: black;
                    }
                }
                .withdraw{
                    border-radius: 5px;
                    width: 100%;
                    height: auto;
                    background-color: #cccccc;
                    color: #fff;
                    text-align: center;
                }
            }
        }
        .dealList{
            h4{
                font-size: 14px;
                text-indent: 10px;
                color: #b199a5;
            }
            div{
                text-align: center;
                p{
                    font-size: 16px;
                    margin-top: 5px;
                    color: #666;
                }
                p:nth-of-type(2){
                    font-size: 12px;
                }
            }
        }
    }
</style>



