<template>
    <div class="downloadContainer">
        <img src="data:image/jpeg;base64," alt="">
        <p>下载饿了么APP</p>
        <el-button type="text" @click="open">下载APP</el-button>
    </div>
</template>

<script>
export default {
    date(){
        return {}
    },
    methods:{
        open() {
            this.$alert('请至应用商店下载APP', {
                confirmButtonText: '确定',
                center: true,
                customClass: 'tishixiaoxi'
            })
        }
    }
}
</script>

<style lang='scss' scoped>
    .downloadContainer{
        text-align: center;
        img{
            // display: block;
            margin-top: 30px;
            border-radius: 20px;
            left: 50%;
        }
        p{
            font-size: 18px;
            color: #666;
            margin-top: 5px;
        }
        button{
            text-align: center;
            width: 90%;
            box-sizing: border-box;
        }
    }
    .tishixiaoxi{
        width: 200px;
        height: auto;
    }
</style>



