<template>
    <div class="moneyContainer">
        <div class="money">
            <div>
                <p class="line1">当前余额 <span class="glyphicon glyphicon-question-sign" @click="goToMoneyInfo()">余额说明</span></p>
                <p class="line2"><b>{{money}}</b>元</p>
                <div class="withdraw">提现</div>
            </div>
        </div>
        <div class="dealList">
            <h4>交易明细</h4>
            <div>
                <img src="data:image/png;base64," alt="">
                <p>暂无明细记录</p>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data(){
        return {
            money: 0.00
        }
    },
    methods:{
        goToMoneyInfo(){
            this.$router.push({name: 'answer', query:{key: 'balanceCaption'}})
        }
    }
}
</script>

<style lang='scss' scoped>
    .moneyContainer{
        background-color: #f5f5f5;
        .money{
            background-color: #3190e8;
            padding: 10px;
            div{
                padding: 10px;
                background-color: #fff;
                border-radius: 10px;
                .line1{
                    margin: 0;
                    color: #666666;
                    .glyphicon{
                        float: right;
                        color: #3190e8;
                        font-size: 12px;
                    }
                }
                .line2{
                    margin: 0;
                    b{
                        font-size: 35px;
                        color: black;
                    }
                }
                .withdraw{
                    border-radius: 5px;
                    width: 100%;
                    height: auto;
                    background-color: #cccccc;
                    color: #fff;
                    text-align: center;
                }
            }
        }
        .dealList{
            h4{
                font-size: 14px;
                text-indent: 10px;
                color: #b199a5;
            }
            div{
                text-align: center;
                p{
                    font-size: 16px;
                    margin-top: 5px;
                    color: #666;
                }
            }
        }
    }
</style>



